@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.topcontent{
    color: white;

}
.topcontent__container{
    position: absolute;
    top: 300px;
    right: 150px;

}
.topcontent__container>p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}
.topcontent__container>a>button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
} 
.topcontent__container>h1{
    font-size: 50px;
    font-family: "Montserrat",sans-serif;
} 
.topcontent__downloadbtn{
    color: white;
    background-color: goldenrod;
    font-size: 14px;
    padding: 10px 30px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
}
.topcontent__workbtn{
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
}
.topcontent__downloadbtn:hover{
    color: goldenrod;
    background-color: white;
    font-weight: 600;
}
.topcontent__workbtn:hover{
    color: black;
    background-color: white;
    font-weight: 600;

}