@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap'); 
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap'); 

.header{
    display: flex;
    align-items: center;
    color: white;
    background-color: black;
    top: 0;
    position: sticky;
    z-index: 2000;
}
.header__left {
    flex: 0.4;
}
.header__left>h1{
    text-align: center;
    font-family: "Alata",serif;
    letter-spacing: 1px;
    padding: 20px 0;
} 
.header__left>h1>span{
    color: goldenrod;
}
.header__right{
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
} 
.header__right>a>h4{
    font-size: 18px;
    cursor: pointer;
    transition: 200ms ease-in-out;
    font-family: "Crimson text",serif;
    padding: 20px 0;
    letter-spacing: 1px;

}
.header__right>a>h4:hover{
    transform: scale(1.09);
    background-color: goldenrod;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: "Crimson text",serif;
    font-size: 18px;
    cursor: pointer;
}